* {
    outline: none !important;
    -moz-outline: none !important;
}

/* Main */
body {
    overflow-x: hidden;
    margin-top: -10px;
}

body {
    background: #49CCED;
}

#header {
    background-color: #2e363f;
    margin-top: 10px;
}

#search input[type=text] {
    background-color: #fff;
    border-left: 1px solid #e3ebed;
}

#search input[type=text]:focus {
    color: #777777;
}

.dropdown-menu li a:hover, .dropdown-menu .active a, .dropdown-menu .active a:hover {
    color: #eeeeee;
    background: #F77825;
}

.rightzero {
    right: 0px;
    display: none;
}

@media (max-width: 480px) {
    #sidebar > a {
        background: #49CCED;
    }

    #sidebar > ul {
        background-color: #49CCED;
    }

    #user-nav {
        margin: 0px auto;
        top: -31px;
        border-top: none;
        width: 250px;
        left: -3%;
        text-align: center;
        float: none;
    }

    #user-nav > ul > li:last-child {
        border-right: 1px solid #363E48;
    }

    #user-nav > ul > li {
        border-top: 1px solid #363E48;
    }

    #sidebar {
        top: -30px;
    }

    #content {
        margin-top: -30px;
    }
}

@media (min-width: 481px) and (max-width: 767px) {
    #sidebar > ul ul:before {
        border-right: 7px solid rgba(0, 0, 0, 0.2);
    }

    #sidebar > ul ul:after {
        border-right: 6px solid #222222;
    }
}

a:hover {
    text-decoration: none;
}

/*bootstrap-overwrite*/
select, textarea, input[type="text"], input[type="password"], input[type="datetime"], input[type="datetime-local"], input[type="date"], input[type="month"], input[type="time"], input[type="week"], input[type="number"], input[type="email"], input[type="url"], input[type="search"], input[type="tel"], input[type="color"], .uneditable-input, .label, .dropdown-menu, .btn, .well, .progress, .table-bordered, .btn-group > .btn:first-child, .btn-group > .btn:last-child, .btn-group > .btn:last-child, .btn-group > .dropdown-toggle, .alert {
    border-radius: 0px;
}

.btn, textarea, input[type="text"], input[type="password"], input[type="datetime"], input[type="datetime-local"], input[type="date"], input[type="month"], input[type="time"], input[type="week"], input[type="number"], input[type="email"], input[type="url"], input[type="search"], input[type="tel"], input[type="color"], .uneditable-input {
    box-shadow: none;
}

.progress, .progress-success .bar, .progress .bar-success, .progress-warning .bar, .progress .bar-warning, .progress-danger .bar, .progress .bar-danger, .progress-info .bar, .progress .bar-info, .btn, .btn-primary {
    background-image: none;
}

/* Header */
#header {
    height: 62px;
    position: relative;
    width: 100%;
    z-index: -7;
}

#header h1 {
    background: url("/images/logo.png") no-repeat scroll 0 0 transparent;
    height: 31px;
    left: 15px;
    line-height: 600px;
    overflow: hidden;
    position: relative;
    top: 20px;
    width: 250px;
    margin-top: 0;
}

#header h1 a {
    display: block;
}

.fl {
    float: left;
}

.fr {
    float: right;
}

/* Search input */
#search {
    position: relative;
    z-index: 1;
    top: 42px;
    float: right;
}

#search input[type=text] {
    border-radius: 0px 0 0 0px;
    padding: 8px 10px;
    border: 1px;
    line-height: 24px;
    width: 166px;
}

#search button {
    border: 0;
    margin-top: -11px;
    padding: 8px 10px;
    margin-left: -5px;
    background-color: #2e363f;
}

#search button i {
    opacity: 0.5;
}

#search button:hover i, #search button:active i {
    opacity: 0.9;
}

/* Top user navigation */
#sidebar {
    width: 100%;
    background: #49cced;
    position: absolute;
    clear: both;
    top: 62px;
}

#sidebar > ul {
    margin: 0px;
    padding: 0px;
    width: 100%;
    display: block;
    z-index: 999;
}

#sidebar > ul > li {
    list-style-type: none;
    float: left;
    display: block;
    margin: 0px;
    border-right: 1px solid #41bedd;
    position: relative;
    padding: 10px;
    cursor: pointer;
}

#sidebar > ul > li a {
    padding: 12px 0;
}

#sidebar > ul > li:hover ul {
    display: block;
}

#sidebar > ul > li:hover {
    background-color: #41bedd;
}

#sidebar > ul > li:hover a {
    background: none;
}

#sidebar > ul li ul {
    margin: 0px;
    padding: 0px;
    display: none;
    z-index: 999;
    position: absolute;
    left: 0px;
    top: 40px;
    background: #41bedd;
    min-width: 200px;
}

#sidebar > ul li ul li {
    list-style-type: none;
    margin: 0px;
    font-size: 12px;
    line-height: 30px;
}

#sidebar > ul li ul li a {
    display: block;
    padding: 5px 10px;
    color: #fff;
    text-decoration: none;
    font-weight: bold;
}

#sidebar > ul li ul li:hover a {
    background-color: #36a8c5;
}

#sidebar > ul li span {
    cursor: pointer;
    margin: 0px 2px 0 5px;
    font-weight: bold;
    color: #fff;
    font-size: 11px;
}

#sidebar > ul li a i {
    margin-top: 4px;
    vertical-align: top;
}

#my_menu_input {
    display: none;
}

#user-nav {
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 11;
    margin: 0;
}

#user-nav > ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

#user-nav > ul > li {
    float: left;
    list-style-type: none;
    margin: 0;
    position: relative;
    padding: 6px 0;
    background: #2e363f;
    border-left: 1px solid #363e48;
}

#user-nav > ul > li:hover {
    background: #232a32;
}

#user-nav > ul > li > a {
    padding: 15px 10px;
    display: block;
    font-size: 12px;
}

#user-nav > ul > li > a:hover, #user-nav > ul > li.open > a {
    color: #ffffff;
}

#user-nav > ul > li > a > i, #sidebar li a i {
    vertical-align: top;
    margin-top: 2px;
}

#user-nav > ul > li > a:hover > i, #user-nav > ul > li.open > a > i {
    opacity: 1;
}

#user-nav > ul > li > a > .label {
    vertical-align: middle;
    padding: 1px 4px 1px;
    margin: -2px 4px 0;
    display: inline-block;
}

#user-nav > ul ul > li > a {
    text-align: left;
    text-shadow: 0 1px 0 #ffffff;
}

#user-nav > ul ul > li > a:hover {
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.5);
}

/* Content */
#content {
    background: url(/images/bodyBg.png) repeat #fff;
    margin-left: 0px;
    margin-right: 0;
    margin-top: 80px;
    padding-bottom: 25px;
    position: relative;
    min-height: 500px;
    width: auto;
}

#content-header {
    position: abslute;
    width: 100%;
    margin-top: -38px;
    z-index: 20;
}

#content-header h1 {
    color: #555555;
    font-size: 28px;
    font-weight: normal;
    float: none;
    text-shadow: 0 1px 0 #ffffff;
    margin-left: 20px;
    position: relative;
}

#content-header .btn-group {
    float: right;
    right: 20px;
    position: absolute;
}

#content-header h1, #content-header .btn-group {
    margin-top: 20px;
}

#content-header .btn-group .btn {
    padding: 11px 14px 9px;
}

#content-header .btn-group .btn .label {
    position: absolute;
    top: -7px;
}

.container-fluid .row-fluid:first-child {
    margin-top: 20px;
}

/* Breadcrumb */
#breadcrumb {
    background-color: #fff;
    border-bottom: 1px solid #e3ebed;
}

#breadcrumb a {
    padding: 8px 20px 8px 10px;
    display: inline-block;
    background-image: url("/images/breadcrumb.png");
    background-position: center right;
    background-repeat: no-repeat;
    font-size: 11px;
    color: #666666;
}

#breadcrumb a:hover {
    color: #333333;
}

#breadcrumb a:last-child {
    background-image: none;
}

#breadcrumb a.current {
    font-weight: bold;
    color: #444444;
}

#breadcrumb a i {
    margin-right: 5px;
    opacity: .6;
}

#breadcrumb a:hover i {
    margin-right: 5px;
    opacity: .8;
}

/* Style Switcher */
#style-switcher {
    position: absolute;
    width: 220px;
    height: 30px;
    background-color: #000000;
    z-index: 40;
    right: 0;
    top: 123px;
    border-radius: 5px 0 0 5px;
    margin-right: -190px;
}

#style-switcher i {
    display: inline-block;
    margin: -5px 10px 0 10px;
}

#style-switcher i:hover {
    cursor: pointer;
}

#style-switcher span {
    font-weight: bold;
    color: #ffffff;
    display: inline-block;
    margin: -15px 20px 0 0;
    vertical-align: middle;
}

#style-switcher a {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-top: 4px;
    border-style: solid;
    border-width: 1px;
    border-color: transparent;
}

/* Footer */
#footer {
    text-align: center;
    color: #777777;
    padding: 10px 0;
    background: #2e363f;
}

