    height: 100%;
    background: url(overlay.png) repeat #000;
    text-align: center;
    z-index: 15;
}

#lightbox p {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 22px;
    height: 22px;
    cursor: pointer;
    z-index: 22;
    border: 1px solid #fff;
    border-radius: 100%;
    padding: 2px;
    text-align: center;
    transition: 0.5s;
}

#lightbox p:hover {
