        padding: 0px;
        top: 35px;
        z-index: 999;
        display: none;
        position: absolute;
        background: #41BEDD;
        width: 100%;
        min-width: 100%;
        border-radius: none;
    }

    #sidebar > ul li ul li {
        list-style-type: none;
        margin: 0px;
        font-size: 12px;
        line-height: 30px;
    }

    #sidebar > ul li ul li a {
