        padding: 5px 10px;
        color: #fff;
        text-decoration: none;
        font-weight: bold;
    }

    #sidebar > ul li ul li:hover a {
        background-color: #36A8c5;
        border-radius: 0px;
    }

    #sidebar > ul li span {
        cursor: pointer;
        margin: 0px 2px 0 5px;
        font-weight: bold;
        color: #fff;
        font-size: 12px;
    }

    #sidebar > ul li a i {
        margin-top: 4px;
        vertical-align: top;
    }

    #sidebar > a {
        padding: 9px 20px 9px 15px;
        display: block !important;
        color: #eeeeee;
        float: none !important;
        font-size: 12px;
        font-weight: bold;
    }

    #sidebar > ul > li > a {
        padding: 5px;
