        top: 20px;
    }

    #search {
        top: 0px;
    }

    #my_menu {
        display: none;
    }

    #my_menu_input {
        display: block;
    }

    #content {
        margin-top: 38px;
    }

    #sidebar > ul > li {
        float: none;
    }

    #sidebar > ul > li:hover ul {
        display: block;
    }

    #sidebar, #sidebar > ul {
        width: 43px;
        display: block;
        position: absolute;
        background: #49CCED;
        height: 100%;
        z-index: 1;
    }

    #sidebar > ul ul {
        display: none;
        position: absolute;
        left: 50px;
        top: 0;
        min-width: 150px;
        list-style: none;
    }

    #sidebar > ul ul li a {
        white-space: nowrap;
        padding: 10px 25px;
    }

    #sidebar > ul ul:before {
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        content: "";
        display: inline-block;
        left: -6px;
        position: absolute;
        top: 11px;
    }

    #sidebar > ul ul:after {
        border-top: 6px solid transparent;
        border-bottom: 6px solid transparent;
        content: "";
        display: inline-block;
        left: -5px;
        position: absolute;
        top: 12px;
    }

    #sidebar > a {
        display: none !important;
    }

    #sidebar > ul > li.open.submenu > a {
        border-bottom: none !important;
    }

    #sidebar > ul > li > a > span {
        display: none;
    }

    #content {
        margin-left: 43px;
    }
